
export class Exercise {
    public constructor (name,rep,set,weight ){
        this.name = name;
        this.rep = rep;
        this.set = set;
        this.weight = weight;

    }

    public  name:string;
    public  rep:number;
    public  set:number;
    public weight:number;
  
}


