export class Trainer {
    constructor(name, phone, age, experience) {
        this.name = name;
        this.phone = phone;
        this.age = age;
        this.experience = experience;
    }
    name: string
    phone: string;
    photo: ImageData;
    age: number;
    experience: number;

}


